import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as actionCreators from '../store/actions'

import DetailContent from '../component/Detail/DetailContent'

class Detail extends Component {
  componentDidMount () {
    const {setDataLoaded, getComicList, match} = this.props

    setDataLoaded(false)
    getComicList({key: match.params.comicKey})
  }

  render () {
    return (
      <main>
        <DetailContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    selectComic: state.selectComic,
    selectChapter: state.selectChapter,
    comicList: state.comicList,
    isDataLoaded: state.isDataLoaded,
    isModalOpen: state.isModalOpen
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getComicList: (query) => dispatch(actionCreators.getComicList(query)),
    updateComicItem: (comic) => dispatch(actionCreators.updateComicItem(comic)),
    setSelectChapter: (chapter) => dispatch(actionCreators.setSelectChapter(chapter)),
    setComicHistory: (comic, chapter) => dispatch(actionCreators.setComicHistory(comic, chapter)),
    toggleLocalFavorite: (comic) => dispatch(actionCreators.toggleLocalFavorite(comic)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    toggleModal: () => dispatch(actionCreators.toggleModal())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Detail))



