import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import withWidth from '@material-ui/core/withWidth'

import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight'
import SwipeableViews from 'react-swipeable-views'

import AppImageLoader from '../../AppImageLoader/AppImageLoader'

const styles = theme => ({
  root: {
    maxWidth: '100vw',
    flexGrow: 1
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    height: 50,
    paddingLeft: theme.spacing.unit * 4,
    marginBottom: 20,
    backgroundColor: theme.palette.background.default
  },
  img: {
    height: '100vh',
    maxWidth: '100vw',
    overflow: 'hidden',
    width: '100%'
  },
  changePageBtn: {
    display: 'inline-block',
    position: 'fixed',
    top: 0,
    height: '100vh',
    padding: '40vh .5rem',
    '&:hover': {
      backgroundColor: 'rgba(0, 0, 0 , .5)',
      color: '#fff'
    },
    cursor: 'pointer'
  },
  leftBtn: {
    left: 0
  },
  rightBtn: {
    right: 0
  }
})

const SwiperComic = (props) => {
  const {
    classes,
    theme,
    contentList,
    isShowControl,
    activeStep,
    handleNext,
    handleBack,
    handleStepChange,
    width
  } = props
  const maxSteps = contentList.length

  return (
    <div className={classes.root}>
      {
        activeStep !== 0 && !isShowControl && width !== 'xs'
          ? (
            <div
              className={`${classes.changePageBtn} ${classes.leftBtn}`}
              onClick={(event) => handleBack(event)}>
              <KeyboardArrowLeft style={{ fontSize: 40 }} />
            </div>
          )
          : null
      }
      {
        activeStep !== maxSteps - 1 && !isShowControl && width !== 'xs'
          ? (
            <div
              className={`${classes.changePageBtn} ${classes.rightBtn}`}
              onClick={(event) => handleNext(event)}>
              <KeyboardArrowRight style={{ fontSize: 40 }} />
            </div>
          )
          : null
      }
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents>
        {contentList.map((content, index) => (
          <div className={classes.img} key={index}>
            <AppImageLoader
              src={content}
              alt='comic'
              backgroundSize='contain' />
          </div>
        ))}
      </SwipeableViews>
    </div>
  )
}

SwiperComic.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
}

export default withWidth()(withStyles(styles, { withTheme: true })(SwiperComic))



