import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'

import AppDetailCardContent from '../Common/AppDetailCard/AppDetailCardContent'
import AppModal from '../Common/AppModal/AppModal'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  }
})

class DetailContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  onToggleFavorite = (comic) => {
    const {
      updateComicItem,
      toggleLocalFavorite
    } = this.props

    comic.isFavorite = !comic.isFavorite

    updateComicItem(comic)
    toggleLocalFavorite(comic)
  }

  onSelectChapter = (chapter, needShowModel = true) => {
    const {setSelectChapter, setComicHistory, toggleModal, comicList} = this.props
    const [comic] = comicList
    setSelectChapter(chapter)
    setComicHistory(comic, chapter)
    if (needShowModel) {
      toggleModal()
    }
  }

  render () {
    const {
      classes,
      isDataLoaded,
      isModalOpen,
      toggleModal,
      comicList,
      selectComic,
      selectChapter,
      setComicHistory
    } = this.props
    const isSelectComicExist = (Object.keys(selectComic).length > 0)
    const isSelectChapterExist = (Object.keys(selectChapter).length > 0)
    const [comic] = comicList

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              isDataLoaded
                ? comic
                  ? (
                    <AppDetailCardContent
                      comic={comic}
                      onToggleFavorite={this.onToggleFavorite}
                      onSelectChapter={this.onSelectChapter} />
                  )
                  : (
                    <AppDetailCardContent
                      comic={selectComic}
                      onToggleFavorite={this.onToggleFavorite}
                      onSelectChapter={this.onSelectChapter} />
                  )
                : (
                  isSelectComicExist
                    ? (
                      <AppDetailCardContent
                        comic={selectComic}
                        onToggleFavorite={this.onToggleFavorite}
                        onSelectChapter={this.onSelectChapter} />
                    )
                    : (
                      <div className={classes.loader}>
                        <CircularProgress size={80} />
                      </div>
                    )
                )
            }
          </Grid>
        </Grid>
        {
          isSelectChapterExist
            ? (
              <AppModal
                isModalOpen={isModalOpen}
                toggleModal={toggleModal}
                comic={comic}
                selectChapter={selectChapter}
                onSelectChapter={this.onSelectChapter}
                setComicHistory={setComicHistory} />
            )
            : null
        }
      </div>
    )
  }
}

export default withStyles(styles)(DetailContent)



