import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
// import * as CONSTANT from '../../../constant'

import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog'
import withMobileDialog from '@material-ui/core/withMobileDialog'
import DialogContent from '@material-ui/core/DialogContent'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
// import LinearProgress from '@material-ui/core/LinearProgress'
import Slider from '@material-ui/lab/Slider'

import SwiperComic from './SwiperComic/SwiperComic'

const styles = theme => ({
  root: {
    width: '100%'
  },
  viewerTheme: {
    background: 'linear-gradient(to right, rgba(63, 81, 181, .7), rgba(63, 81, 181, .5), rgba(63, 81, 181, .7))'
  },
  viewerThemeMask: {
    background: 'linear-gradient(to right, rgba(0, 0, 0, .7), rgba(0, 0, 0, .3), rgba(0, 0, 0, .7))'
  },
  viewerTitle: {
    width: '100%',
    color: '#fff'
  },
  comicPage: {
    width: '100%',
    maxWidth: '90vw'
  },
  message: {
    margin: '8rem 0',
    textAlign: 'center'
  },
  viewerFooterBar: {
    display: 'inline-block',
    position: 'fixed',
    bottom: 0,
    width: '100%',
    ...theme.mixins.toolbar
  },
  viewerFooter: {
    padding: '.7rem .5rem'
  }
})

class AppModal extends Component {
  constructor () {
    super()
    this.state = {
      isShowControl: false,
      activeStep: 0
    }
  }

  toggleControl = () => {
    this.setState({
      isShowControl: !this.state.isShowControl
    })
  }

  handleNext = (event) => {
    event.stopPropagation()
    const {comic, selectChapter, setComicHistory} = this.props
    setComicHistory(comic, selectChapter.no + 1)
    this.setState(prevState => ({
      activeStep: prevState.activeStep + 1
    }))
  }

  handleBack = (event) => {
    event.stopPropagation()
    this.setState(prevState => ({
      activeStep: prevState.activeStep - 1
    }))
  }

  handleStepChange = activeStep => {
    this.setState({ activeStep })
  }

  handleScrollSlide = (event, value) => {
    if (value === 0) return
    this.setState({activeStep: value - 1})
  }

  render () {
    const {isShowControl, activeStep} = this.state
    const {
      classes,
      isModalOpen,
      toggleModal,
      comic,
      selectChapter,
      onSelectChapter
    } = this.props
    const {no, name, contentList} = selectChapter || {}
    const isChapterExist = (Object.keys(selectChapter).length > 0)

    return (
      <Dialog
        open={isModalOpen}
        onClose={toggleModal}
        fullScreen
        classes={{
          paper: classes.root
        }}>
        {
          isShowControl
            ? (
              <AppBar className={classes.viewerTheme}>
                <Toolbar className={classes.viewerThemeMask}>
                  <Typography variant='title' className={classes.viewerTitle}>
                    {
                      isChapterExist
                        ? <span>CH{no}. {name}</span>
                        : null
                    }
                  </Typography>
                  {
                    (
                      activeStep === (contentList.length - 1) &&
                      selectChapter.no !== (comic.chapterList.length - 1)
                    )
                      ? (
                        <Button
                          variant='contained'
                          color='primary'
                          onClick={() => {
                            this.setState({activeStep: 0})
                            onSelectChapter(comic.chapterList[selectChapter.no + 1], false)
                          }}
                          style={{marginRight: '.5rem'}}>
                          NEXT
                        </Button>
                      )
                      : null
                  }
                  <Button variant='contained' color='primary' onClick={toggleModal}>
                    CANCEL
                  </Button>
                </Toolbar>
              </AppBar>
            )
            : null
        }
        <DialogContent onClick={this.toggleControl} style={{padding: 0}}>
          <Grid container justify='center' alignItems='center'>
            <Grid item className={classes.comicPage}>
              {
                isChapterExist
                  ? (
                    <SwiperComic
                      contentList={contentList}
                      isShowControl={isShowControl}
                      activeStep={activeStep}
                      handleNext={this.handleNext}
                      handleBack={this.handleBack}
                      handleStepChange={this.handleStepChange} />
                  )
                  : (
                    <div className={classes.message}>
                      <Typography
                        variant='display2'
                        color='error'
                        className={classes.message}
                        gutterBottom>
                        Comic isn't exist
                      </Typography>
                    </div>
                  )
              }
            </Grid>
          </Grid>
        </DialogContent>
        {
          isShowControl
            ? (
              <div className={`${classes.viewerTheme} ${classes.viewerFooterBar}`}>
                <div className={`${classes.viewerThemeMask} ${classes.viewerFooter}`}>
                  <Typography
                    variant='body2'
                    align='center'
                    style={{color: '#fff'}}
                    gutterBottom>
                    {activeStep + 1} / {contentList.length}
                  </Typography>
                  <Slider
                    value={activeStep + 1}
                    max={contentList.length}
                    min={0}
                    step={1}
                    onChange={this.handleScrollSlide} />
                </div>
              </div>
            )
            : null
        }
      </Dialog>
    )
  }
}

export default withStyles(styles)(withMobileDialog()(AppModal))



