import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../../constant'
import formatDistance from 'date-fns/formatDistance'

import IconButton from '@material-ui/core/IconButton'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import CardActions from '@material-ui/core/CardActions'
import Typography from '@material-ui/core/Typography'
import ListSubheader from '@material-ui/core/ListSubheader'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import CircularProgress from '@material-ui/core/CircularProgress'

import FavoriteIcon from '@material-ui/icons/Favorite'
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder'
import StarIcon from '@material-ui/icons/Star'
import StarBorderIcon from '@material-ui/icons/StarBorder'
import BookIcon from '@material-ui/icons/Book'

import AppImageLoader from '../AppImageLoader/AppImageLoader'

const styles = theme => ({
  root: {
    margin: '1rem 0',
    [theme.breakpoints.only('xs')]: {
      margin: '1rem auto',
      width: 'calc(100% - 2rem)'
    }
  },
  message: {
    margin: '5rem 0',
    textAlign: 'center'
  },
  favoriteIcon: {
    color: 'red'
  },
  cover: {
    width: 'auto',
    height: '40vh',
    maxHeight: 500
  },
  rateIcon: {
    fontSize: '1rem',
    color: 'orange'
  },
  lastChapter: {
    background: 'linear-gradient(to right, red, orange, red)',
    color: '#fff',
    margin: '.5rem',
    width: 'calc(100% - 1rem)'
  },
  chapterList: {
    display: 'block'
  }
})

const AppDetailCardContent = (props) => {
  const {
    classes,
    comic,
    onToggleFavorite,
    onSelectChapter
  } = props
  const {
    cover,
    title,
    description,
    chapterList,
    isFavorite,
    rate,
    timestamp,
    isLoading
  } = comic || {}
  const isComicExist = (Object.keys(comic).length > 0)

  return (
    <div>
      {
        isComicExist
          ? (
            <Card raised className={classes.root}>
              <CardHeader
                action={
                  <IconButton onClick={() => onToggleFavorite(comic)}>
                    {
                      isLoading
                        ? <CircularProgress size={30} />
                        : (
                          isFavorite
                            ? <FavoriteIcon className={classes.favoriteIcon} />
                            : <FavoriteBorderIcon className={classes.favoriteIcon} />
                        )
                    }
                  </IconButton>
                }
                title={title}
                subheader={`Last Update : ${formatDistance(timestamp, new Date())}`} />
              <div className={classes.cover}>
                <AppImageLoader
                  src={cover}
                  alt={title}
                  loaderSize={60}
                  backgroundSize='cover' />
              </div>
              <CardContent>
                <Typography variant='title' gutterBottom>
                  {[...Array(rate)].map((item, index) => (
                    <StarIcon key={index} className={classes.rateIcon} />
                  ))}
                  {[...Array(CONSTANT.RATE_TOTAL - rate)].map((item, index) => (
                    <StarBorderIcon key={index} className={classes.rateIcon} />
                  ))}
                </Typography>
                <Typography variant='caption' gutterBottom>Description</Typography>
                <Typography variant='body2' gutterBottom>{description}</Typography>
              </CardContent>
              <CardActions className={classes.chapterList}>
                <Button
                  variant='contained'
                  className={classes.lastChapter}
                  onClick={() => onSelectChapter(chapterList[chapterList.length - 1])}>
                  Read Last Chapter
                </Button>
                <List
                  component='nav'
                  subheader={<ListSubheader component='div'>Chapters</ListSubheader>}>
                  {chapterList.map((chapter, index) => (
                    <ListItem
                      button
                      key={index}
                      divider={index !== (chapterList.length - 1)}
                      onClick={() => onSelectChapter(chapter)}>
                      <ListItemIcon>
                        <BookIcon />
                      </ListItemIcon>
                      <ListItemText
                        inset
                        primary={`Ch ${chapter.no} : ${chapter.name}`} />
                    </ListItem>
                  ))}
                </List>
              </CardActions>
            </Card>
          )
          : (
            <div className={classes.message}>
              <Typography variant='display2' color='error' gutterBottom>
                Comic isn't exist
              </Typography>
            </div>
          )
      }
    </div>
  )
}

AppDetailCardContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AppDetailCardContent)



